import React from 'react'
import { AppLink } from '../components/gatsbyWithStyled'
import { H1, H2, Hr } from '../components/Heading'
import { MainBody } from '../components/Containers'
import { rhythm, scale } from '../utils/typography'

class Template extends React.Component {
  render() {
    const { location, children } = this.props
    const rootPath = `${__PATH_PREFIX__}/`
    let header

    if (location.pathname === rootPath) {
      header = (
        <H1 style={{ ...scale(1.5) }}>
          <AppLink to={'/'}>The Minimum Town</AppLink>
        </H1>
      )
    } else {
      header = (
        <H2>
          <AppLink to={'/'}>The Minimum Town</AppLink>
        </H2>
      )
    }
    return (
      <MainBody>
        {header}
        <Hr />
        {children}
      </MainBody>
    )
  }
}

export default Template
